.has-fade {
  visibility: hidden;
}

@keyframes fade-in {
  from {
    visibility: hidden;
    opacity: 0;
  }
  1% {
    visibility: visible;
    opacity: 0;
  }
  to {
    visibility: visible;
    opacity: 1;
  }
}
.fade-in {
  animation: fade-in 200ms ease-in-out forwards;
}

@keyframes fade-out {
  from {
    visibility: visible;
    opacity: 1;
  }
  99% {
    visibility: visible;
    opacity: 0;
  }
  to {
    visibility: hidden;
    opacity: 0;
  }
}
.fade-out {
  animation: fade-out 200ms ease-in-out forwards;
}

@keyframes rotate {
  from {
    filter: drop-shadow(10px 5px 3px black);
    transform: rotate(0deg);
  }
  50% {
    filter: drop-shadow(8px 5px 3px black);
    transform: rotate(180deg);
  }
  80% {
    filter: drop-shadow(5px 5px 2px black);
  }
  to {
    filter: drop-shadow(10px 5px 2px black);
    transform: rotate(360deg);
  }
}
.rotate::after {
  animation: rotate 10s infinite linear;
}

.rotate_only {
  animation: rotate 3s infinite linear;
}

@keyframes cdp-in {
  from {
    transform: scale(1.5);
    opacity: 0;
  }
  to {
    transform: scale(1);
    opacity: 1;
  }
}
/*Boilerplate styles for the website*/
/*Change the box sizing to border box so the margin and padding does not add and increse the size of the element
 and font size to 100%*/
html {
  box-sizing: border-box;
  font-size: 100%;
}

/*Changing the color of the testimonial section arrows*/
:root {
  --swiper-pagination-color: hsl(45, 94%, 56%);
}

/*Removing the default padding and margin from all the elements*/
*,
*::before,
*::after {
  box-sizing: inherit;
  padding: 0;
  margin: 0;
}

/*Body Styles*/
body {
  font-family: "Roboto", sans-serif;
  font-size: 0.875rem;
  font-weight: 600;
  background-color: hsl(225, 13%, 6%);
  line-height: 1.2;
  margin: 0;
  padding: 0;
  color: hsl(34, 21%, 94%);
}
@media (min-width: 64em) {
  body {
    font-size: 1.125rem;
  }
}

input {
  outline: none;
}

/*Making different classes to use in the html page to make the code more readable */
.container {
  max-width: 120rem;
  margin: 0 auto;
}
.container--pall {
  padding-top: 2.25rem;
  padding-bottom: 2.25rem;
  padding-left: 1.5rem;
  padding-right: 1.5rem;
}
.container--px {
  padding-left: 1.5rem;
  padding-right: 1.5rem;
}
.container--py {
  padding-top: 2.25rem;
  padding-bottom: 2.25rem;
}
.container--pt {
  padding-top: 2.25rem;
}
.container--pb {
  padding-bottom: 2.25rem;
}
.container--pl {
  padding-left: 1.5rem;
}
.container--pr {
  padding-right: 1.5rem;
}

.flex {
  display: flex;
}
.flex-jc-sb {
  justify-content: space-between;
}
.flex-jc-c {
  justify-content: center;
}
.flex-ai-c {
  align-items: center;
}
.flex-gap {
  gap: 1.25rem;
}

/*Button styles to be used in the website**/
.button {
  padding: 1rem 1.875rem;
  color: hsl(225, 13%, 6%) !important;
  background-color: hsl(45, 94%, 56%);
  border-radius: 50px;
  transition: all 0.3s ease-in-out;
  cursor: pointer;
  display: inline-block;
}

.button:hover {
  box-shadow: 0px 10px 15px hsl(45, 94%, 56%);
  color: hsl(34, 21%, 94%) !important;
  transform: translateY(-5px);
  background: linear-gradient(45deg, hsl(45, 94%, 56%), hsl(225, 13%, 6%));
}

.button__outline {
  padding: 1rem 1.875rem;
  border: 2px solid hsl(45, 94%, 56%);
  border-radius: 50px;
  transition: all 0.3s ease 0s;
  cursor: pointer;
  display: inline-block;
  color: hsl(45, 94%, 56%) !important;
}

.button__outline:hover {
  box-shadow: 0px 10px 15px hsl(45, 94%, 56%);
  color: hsl(34, 21%, 94%) !important;
  transform: translateY(-5px);
  background: linear-gradient(45deg, hsl(45, 94%, 56%), hsl(225, 13%, 6%));
}

/*Classes for the responsiveness of the nav bar and hamburger menu*/
@media (max-width: 63.875em) {
  .hide-for-mobile {
    display: none;
  }
}

@media (min-width: 64em) {
  .hide-for-desktop {
    display: none;
  }
}

/*Boilerplate styles for the typography elements in the website*/
h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.15;
  color: hsl(34, 21%, 94%);
  font-weight: 300;
}

h1 {
  font-size: 2.31rem;
  margin-bottom: 1.5rem;
}
@media (min-width: 64em) {
  h1 {
    font-size: 3.25rem;
  }
}

h2 {
  font-size: 1.875rem;
  margin-bottom: 1.5625rem;
}
@media (min-width: 64em) {
  h2 {
    font-size: 2.25rem;
    margin-bottom: 2.25rem;
  }
}

p {
  line-height: 1.7;
  margin-bottom: 2.25rem;
}

a,
a:visited,
a:active {
  text-decoration: none;
  color: hsl(34, 21%, 94%);
}

li {
  list-style: none;
}

/*Added the boilerplate styles for the webpage and typography to be used in the final styles file*/
/*Header Styles*/
/*Default mobile styles*/
/*Added breakpoints from breakpoints.scss file*/
.header {
  position: relative;
  z-index: 1;
  /*Styles for overlay behind the toggle menu*/
}
.header.open .header__toggle > span:first-child {
  transform: translateY(20px) rotate(45deg);
}
.header.open .header__toggle > span:nth-child(2) {
  transform: scaleX(0);
}
.header.open .header__toggle > span:last-child {
  transform: translateY(-20px) rotate(-45deg);
}
.header .overlay {
  opacity: 0;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: linear-gradient(hsla(45, 94%, 56%, 0.531), transparent);
}
.header nav {
  position: relative;
  background-color: hsl(225, 13%, 6%);
  padding-top: 1.0625rem;
  padding-bottom: 1.0625rem;
}
.header__logo img {
  width: 8.8125rem;
  height: 2rem;
}
.header__toggle > span {
  display: block;
  width: 35px;
  height: 3px;
  background-color: hsl(45, 94%, 56%);
  border-radius: 5px;
  transition: all 300ms ease-in-out;
  transform-origin: 33px 1px;
}
.header__toggle > span:not(:last-child) {
  margin-bottom: 6px;
}
.header__menu {
  position: absolute;
  width: calc(100% - 3rem);
  left: 50%;
  transform: translateX(-50%);
  margin-top: 1.5rem;
  background: hsl(225, 13%, 6%);
  padding: 1.625rem;
  border-radius: 6px;
}
.header__menu a {
  display: block;
  padding: 0.625rem;
  color: hsl(34, 21%, 94%);
  text-align: center;
}
.header__links a {
  font-size: 0.875rem;
  color: hsla(0, 0%, 100%, 0.581);
  transition: color 300ms ease-in-out;
  position: relative;
}
.header__links a:not(:last-child) {
  margin-right: 35px;
}
.header__links a::before {
  content: "";
  display: block;
  position: absolute;
  height: 5px;
  left: 0;
  right: 0;
  bottom: -34.5px;
  background-color: hsl(45, 94%, 56%);
  opacity: 0;
  transition: opacity 300ms ease-in-out;
}
.header__links a:hover {
  color: hsl(34, 21%, 94%);
}
.header__links a:hover::before {
  opacity: 1;
}

/*Footer Styles*/
/*Included styles for desktop and tablet screens*/
/*Added breakpoints from breakpoints.scss file*/
/*Default mobile styles*/
.footer {
  background-color: hsl(34, 21%, 94%);
}
.footer__content {
  background-color: hsl(34, 21%, 94%);
  color: hsl(225, 13%, 6%);
  padding: 2.5rem;
}
@media (min-width: 64em) {
  .footer__content {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: repeat(2, 1fr), auto;
    grid-template-areas: "logo links1 links2 contact" "social links1 links2 contact" "copyright copyright copyright copyright";
  }
}
.footer a {
  color: hsl(225, 13%, 6%);
  font-size: 1.125rem;
  line-height: 2;
}
.footer h2 {
  color: hsl(225, 13%, 6%);
  margin-bottom: 1rem;
}
.footer__logo {
  margin-bottom: 1.875rem;
  display: inline-block;
}
@media (min-width: 64em) {
  .footer__logo {
    grid-area: logo;
  }
}
.footer__logo img {
  width: 8.8125rem;
  height: 2rem;
}
.footer__social {
  margin-bottom: 1.875rem;
}
@media (min-width: 64em) {
  .footer__social {
    grid-area: social;
    align-self: center;
  }
}
.footer__social a {
  display: inline-block;
}
.footer__social a:not(:last-child) {
  margin-right: 1rem;
}
.footer__links {
  display: flex;
  flex-direction: column;
}
.footer__links.col1 {
  margin-bottom: 1.875rem;
}
@media (min-width: 64em) {
  .footer__links.col1 {
    grid-area: links1;
  }
}
@media (min-width: 64em) {
  .footer__links.col1 a {
    line-height: 3.6;
  }
}
.footer__links.col2 {
  margin-bottom: 1.875rem;
}
@media (min-width: 64em) {
  .footer__links.col2 {
    grid-area: links2;
  }
}
.footer__contact {
  display: flex;
  flex-direction: column;
  margin-bottom: 1.875rem;
}
@media (min-width: 64em) {
  .footer__contact {
    grid-area: contact;
    line-height: 3.1;
  }
}
.footer__contact img {
  width: 2rem;
}
.footer__contact li {
  display: flex;
  align-items: center;
}
.footer__contact li span {
  margin-bottom: 0.625rem;
}
.footer__contact li span:nth-child(2) {
  margin-left: 0.625rem;
}
.footer__copyright {
  text-align: center;
}
@media (min-width: 64em) {
  .footer__copyright {
    grid-area: copyright;
    margin-top: 1.5rem;
  }
}

/*Added the styles for the footer  and header  to be used in the final styles file*/
/*Styling for the hero section*/
/*Default are the mobile styles*/
/*Added breakpoints and styles for tablet and mobile screens from breakpoints.scss file*/
.hero {
  overflow: hidden;
}
@media (min-width: 64em) {
  .hero .container {
    display: grid;
    position: relative;
    grid-template-columns: 1fr 1fr;
    align-items: center;
  }
}
.hero__image {
  position: relative;
  min-height: 22rem;
}
@media (min-width: 40em) {
  .hero__image {
    height: 32rem;
  }
}
@media (min-width: 64em) {
  .hero__image {
    grid-column-start: 1;
    grid-column-end: 3;
    height: 50rem;
  }
}
.hero__image::after {
  content: "";
  position: absolute;
  height: 100%;
  width: 100%;
  background-image: url("/images/fast-food.png");
  background-size: 54%;
  background-repeat: no-repeat;
  background-position: center center;
}
@media (min-width: 40em) {
  .hero__image::after {
    background-size: 46%;
  }
}
@media (min-width: 64em) {
  .hero__image::after {
    background-size: 30%;
    background-position: center center;
    left: 25%;
  }
}
.hero__image::before {
  content: "";
  position: absolute;
  height: 100%;
  width: 100%;
  background-image: url("/images/wood_1920.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
}
@media (min-width: 64em) {
  .hero__image::before {
    left: -20%;
    right: -20%;
    width: auto;
  }
}
.hero__text {
  text-align: center;
}
@media (min-width: 64em) {
  .hero__text {
    grid-column-start: 1;
    grid-column-end: 2;
    text-align: left;
    position: absolute;
  }
}
.hero__text span {
  font-family: "Caveat", cursive;
  color: hsl(45, 94%, 56%);
}
@media (min-width: 64em) {
  .hero__btn {
    justify-content: start;
  }
}

/*Styling for the features section of the landing page*/
/*Added the styling for tablet and mobile screens from breakpoints.scss file*/
.feature {
  background-color: hsla(44, 21%, 10%, 0.184);
  text-align: center;
}
.feature__intro {
  margin-bottom: 3rem;
}
.feature__grid {
  gap: 25px;
}
@media (min-width: 40em) {
  .feature__grid {
    display: grid;
    grid-template-columns: repeat(3, auto);
  }
}
.feature__item {
  padding: 1.5rem;
  border-radius: 15px;
  background-color: hsl(225, 13%, 6%);
  border: 1px solid hsla(45, 94%, 56%, 0.531);
  background: linear-gradient(45deg, hsla(0, 0%, 83%, 0.144), hsla(44, 22%, 10%, 0.327));
}
@media (max-width: 39.875em) {
  .feature__item:not(:last-child) {
    margin-bottom: 2rem;
  }
}
.feature__icon {
  margin-bottom: 1.8rem;
}
@media (min-width: 64em) {
  .feature__icon {
    margin-bottom: 2.5rem;
  }
}
.feature__title {
  font-size: 1.75rem;
  font-weight: 300;
  line-height: 1.15;
  margin-bottom: 1rem;
}
@media (min-width: 64em) {
  .feature__title {
    font-size: 2.1rem;
  }
}
.feature__description {
  line-height: 1.5;
  font-size: 1.15rem;
}

.stats {
  display: grid;
  grid-template-columns: repeat(3, 0.35fr);
}

.popmenus {
  background-color: hsla(44, 22%, 10%, 0.327);
}

.popmenu__intro {
  text-align: center;
  font-family: "Caveat", cursive;
}
.popmenu__intro h3 {
  color: hsl(45, 94%, 56%);
}
.popmenu__grid {
  display: grid;
  gap: 3rem;
}
@media (min-width: 40em) {
  .popmenu__grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(3, 1fr);
  }
}
@media (min-width: 64em) {
  .popmenu__grid {
    display: grid;
    grid-template-columns: repeat(3, auto);
    grid-template-rows: repeat(2, 1fr);
    padding-left: 5rem;
    padding-right: 5rem;
  }
}
.popmenu__item {
  border-radius: 20px 20px 10px 10px;
  overflow: hidden;
  border: 1px solid hsla(45, 94%, 56%, 0.531);
  display: grid;
  grid-template-rows: auto 1fr auto;
}
.popmenu__image {
  height: 20rem;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
}
.popmenu__text {
  background-color: hsla(0, 0%, 83%, 0.144);
  height: 17.5rem;
}
@media (min-width: 64em) {
  .popmenu__text {
    height: 25rem;
  }
}
.popmenu__title {
  justify-content: space-between;
  font-family: "Montserrat", sans-serif;
}
.popmenu__title h2 {
  color: hsl(34, 21%, 94%);
}
.popmenu__title h2:nth-child(2) {
  color: hsl(45, 94%, 56%);
}
.popmenu__description {
  font-family: "Montserrat", sans-serif;
}
.popmenu__rating {
  background-color: hsla(0, 0%, 83%, 0.144);
}
.popmenu__stars span:not(:last-child) {
