  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background-color: hsl(225, 13%, 6%);
}
@media (max-width: 63.875em) {
  .contacts {
    background: hsl(45, 94%, 56%);
  }
}
.contacts::before {
  content: "";
  position: absolute;
  left: 0;
  width: 50%;
  height: 100%;
  background: hsl(45, 94%, 56%);
}
@media (max-width: 63.875em) {
