}
.contact__contactForm {
  position: absolute;
  padding: 4.375rem 3.125rem;
  padding-left: 15.625rem;
  margin-left: 9.375rem;
  width: calc(100% - 9.375rem);
  height: 100%;
  background: #fff;
  box-shadow: 0 50px 50px rgba(0, 0, 0, 0.25);
  border-radius: 22px;
}
@media (max-width: 63.875em) {
  .contact__contactForm {
    position: relative;
    width: calc(100% - 21.875rem);
    padding-left: 0;
    margin-left: 0;
    padding: 2.5rem;
    height: 34.375rem;
    box-shadow: none;
    border-radius: 0;
  }
}
@media (max-width: 63.875em) {
  .contact__contactForm {
    width: 100%;
    height: auto;
    border-radius: 0;
