/* _timeline.css */
.timeline {
  position: relative;
}
.timeline::before {
  content: "";
  position: absolute;
  top: 0;
  left: 10%;
  width: 0.25rem;
  height: 100%;
  background-color: hsl(0, 0%, 30%);
}
@media (min-width: 64em) {
  .timeline::before {
    left: 50%;
    margin-left: -0.125rem;
  }
}
.timeline__item {
  margin-bottom: 6.25rem;
  position: relative;
}
.timeline__item::after {
  content: "";
  clear: both;
  display: table;
}
.timeline__item:nth-child(2n) .timeline__content {
  float: right;
}
.timeline__item:nth-child(2n) .timeline__content::before {
  content: "";
  right: 40%;
}
@media (min-width: 64em) {
  .timeline__item:nth-child(2n) .timeline__content::before {
    left: inherit;
  }
}
.timeline__item:nth-child(2n) .timeline__date {
  background-color: hsl(45, 94%, 56%);
}
.timeline__item :last-child {
  margin-bottom: 0;
}
.timeline__item-bg {
  -webkit-transition: all 1s ease-out;
  transition: all 1s ease-out;
  color: hsl(0, 0%, 30%);
}
.timeline__item-bg:nth-child(2n) .timeline__station {
  background-color: hsl(0, 0%, 30%);
}
.timeline__item-bg:nth-child(2n) .timeline__content {
  background-color: hsl(45, 94%, 56%);
}
.timeline__item-bg:nth-child(2n) .timeline__content::before {
  background-color: hsl(0, 0%, 30%);
}
.timeline__item-bg:nth-child(2n) .timeline__description {
  color: hsl(34, 21%, 94%);
}
.timeline__item-bg .timeline__station {
  background-color: hsl(0, 0%, 30%);
}
.timeline__item-bg .timeline__content {
  background-color: hsl(45, 94%, 56%);
}
.timeline__item-bg .timeline__content::before {
  background-color: hsl(0, 0%, 30%);
}
.timeline__item-bg .timeline__description {
  color: hsl(34, 21%, 94%);
}
.timeline__station {
  background-color: hsl(45, 94%, 56%);
  width: 2.5rem;
  height: 2.5rem;
  position: absolute;
  border-radius: 50%;
  padding: 0.625rem;
  top: 0;
  left: 10%;
  margin-left: -2.0625rem;
  border: 4px solid hsl(0, 0%, 30%);
  transition: all 0.3s ease-out;
}
@media (min-width: 64em) {
  .timeline__station {
    left: 50%;
    margin-left: -1.875rem;
    width: 3.75rem;
    height: 3.75rem;
    padding: 0.9375rem;
    border-width: 6px;
  }
}
.timeline__content {
  width: 80%;
  background: hsl(34, 21%, 94%);
  border: 4px solid hsl(0, 0%, 30%);
  padding: 1.25rem 1.875rem;
  border-radius: 6px;
  float: right;
  transition: all 0.3s ease-out;
}
@media (min-width: 64em) {
  .timeline__content {
    width: 40%;
    float: inherit;
    padding: 1.875rem 2.5rem;
  }
}
.timeline__content::before {
  content: "";
  position: absolute;
  left: 10%;
  background: hsl(0, 0%, 30%);
  top: 1.25rem;
  width: 10%;
  height: 0.25rem;
  z-index: -1;
  transition: all 0.3s ease-out;
}
@media (min-width: 64em) {
  .timeline__content::before {
    left: 40%;
    top: 1.875rem;
    height: 0.25rem;
    margin-top: -0.125rem;
  }
}
.timeline__date {
  margin: 0;
  padding: 0.5rem 0.75rem;
  font-size: 1.125rem;
  margin-bottom: 0.625rem;
  background-color: hsl(45, 94%, 56%);
  color: hsl(0, 0%, 30%);
  display: inline-block;
  border-radius: 4px;
  border: 2px solid hsl(0, 0%, 30%);
}
.timeline__description {
  margin: 0;
  padding: 0;
  font-size: 1.125rem;
  line-height: 1.5rem;
  font-weight: 300;
  color: hsl(0, 0%, 30%);
}
@media (min-width: 64em) {
  .timeline__description {
    font-size: 1.1875rem;
    line-height: 1.75rem;
  }
}

