@use "../util/" as u;

.geo{

    &__background{
        width: 430px;
        height: 520px;
        position: absolute;
        transform: translate(-50%,-50%);
        left: 50%;
        top: 50%;
    }

    &__shape{
        height: 200px;
        width: 200px;
        position: absolute;
        border-radius: 50%;
        background: linear-gradient(45deg,u.$dark,u.$yellow, hsl(35, 88%, 52%));
        
        
        &:first-child{
            left: -80px;
            top: -80px;
        }
        
        &:last-child{
            right: -80px;
            bottom: -80px;
           }
    }
}


.sign{
    height: 520px;
    width: 400px;
    background-color: rgba(255,255,255,0.13);
    position: absolute;
    transform: translate(-50%,-50%);
    top: 50%;
    left: 50%;
    border-radius: 10px;
    backdrop-filter: blur(7px);
    border: 2px solid rgba(255,255,255,0.1);
    box-shadow: 0 0 40px rgba(8,7,16,0.6);
    padding: 50px 35px;


    *{
        font-family: 'Poppins',sans-serif;
        color: u.$white;
        letter-spacing: 0.5px;
        outline: none;
        border: none;

    }

    h3{
        font-size: 32px;
        font-weight: 500;
        line-height: 42px;
        text-align: center;
    }

    &__label{
        display: block;
        margin-top: 30px;
        font-size: 16px;
        font-weight: 500;
    }

    &__input{
        display: block;
        height: 50px;
        width: 100%;
        background-color: rgba(255,255,255,0.07);
        border-radius: 3px;
        padding: 0 10px;
        margin-top: 8px;
        font-size: 14px;
        font-weight: 300;

        &::placeholder{
            color: #e5e5e5;
        }


        &:focus{
            border: 2px solid u.$lightyellow;
        }
    }

    &__button{
        margin-top: 50px;
        width: 100%;
        background-color: u.$yellow;
        color: #080710;
        padding: 15px 0;
        font-size: 18px;
        font-weight: 600;
        border-radius: 5px;
        cursor: pointer;
    }
}
